import React from "react";

import { Price } from "./price";
import { data } from "../data";
import { ProfitLoss } from "./profitLoss";
import LineChart from "react-linechart";
import "../../node_modules/react-linechart/dist/styles.css"


export class Portfolio extends React.Component {

    // Create State
    state = {
        numBitcoins: 4
    };
    // react methods componentDidMount() , componentDidUpdate()
    componentDidMount() {
        console.log("Portfolio component mounted");
    }
    componentDidUpdate() {
        console.log("Portfolio component updated")
    }

    // Event Handler
    handleChange = evt => {
      this.setState({ numBitcoins: evt.target.value });
    };

    render() {
        // pull in our Open Source npm react linechart module
        const priceLine = {
          color: "red",
          points: []
        };

        //  Expected data set for lineChart module
        const priceData = [];
        const prices = data.map((dataPoint, index) => {
           const price = dataPoint.ticker.price;
           const time = dataPoint.timestamp;
           priceLine.points.push({
              x: time,
              y: price
           });
           return <Price key={`${index}-${time}`} price={price} time={time}/>
        });
        priceData.push(priceLine);
        // templated Return
        return ( //returned from ProfitLoss, input include state change value
            <div>
                <div>
                    Bitcoins Owned: {" "}
                    <input
                        type="text"
                        value={this.state.numBitcoins}
                        onChange={this.handleChange}
                    />
                </div>
                <ProfitLoss
                    numBitcoins={this.state.numBitcoins}
                    price={data[0].ticker.price}
                    startingPrice={data[data.length - 1].ticker.price}
                />
                Returned from price
                {prices}

                <LineChart width={500} height={400} data={priceData}/>
            </div>
        );
    }
}


